      Download
    </a>
    <ul id="filter-list">
      <li class="red">
        <button @click="addFilter" @keydown.enter="addFilter" @keydown.space="addFilter" data-filter="LESS_RED">-</button>
        <button @click="addFilter" @keydown.enter="addFilter" @keydown.space="addFilter" data-filter="MORE_RED">+</button>
      </li>
      <li class="green">
        <button @click="addFilter" @keydown.enter="addFilter" @keydown.space="addFilter" data-filter="LESS_GREEN">-</button>
        <button @click="addFilter" @keydown.enter="addFilter" @keydown.space="addFilter" data-filter="MORE_GREEN">+</button>
      </li>
      <li class="blue">
        <button @click="addFilter" @keydown.enter="addFilter" @keydown.space="addFilter" data-filter="LESS_BLUE">-</button>
        <button @click="addFilter" @keydown.enter="addFilter" @keydown.space="addFilter" data-filter="MORE_BLUE">+</button>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'render-image',
  components: {},
  props: ['images', 'selected'],
  data() {
    return {
      canvas: null,
      downloadLink: null,
      blob: null,
    }
  },
  mounted() {
    this.canvas = document.querySelector('#render-canvas');
    this.link = document.querySelector('#download-link');
    this.renderSelectedImage();
  },
  watch: {
    images() {this.renderSelectedImage();},
    selected() {this.renderSelectedImage();},
  },
  methods: {
    renderSelectedImage() {
      if(!this.canvas || this.selected == null || !this.images[this.selected]) { return; }

      this.blob = null;
      this.canvas.width = this.images[this.selected].width;
      this.canvas.height = this.images[this.selected].height;
      this.canvas.getContext('2d').putImageData(this.images[this.selected].data, 0, 0);

      this.canvas.toBlob((blob) => {
        this.blob = blob;
      });
    },
    downloadImage() {
      if(!this.blob) { return; }
      this.link.href= URL.createObjectURL(this.blob);
      this.link.download = 'filtered_image.png';
    },
    addFilter(e) {
      this.$emit('filter-add', e.target.getAttribute('data-filter'));
    },
    updated() {
      this.renderSelectedImage();
    }
  }
}
</script>

<style scoped>
#render-image {
  flex: 5;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: flex .5s;
  position: relative;
}

#render-image.collapsed {
  flex: 0;
  overflow: hidden;
}

#render-image canvas {
  max-width: 90%;
  border: 1px solid white;
  margin-bottom: .7em;
  max-height: 15em;
}

#download-link {
  padding: .5em;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  visibility: hidden;
  opacity: 0;
  transition: visibility .5s, opacity .5s;
}

#download-link.active {
  visibility: visible;
  opacity: 1;
}

#download-link, #download-link:hover, #download-link:focus, #download-link:visited, #download-link:active {
  color: white;
  text-decoration: none;
}

#download-link svg {
  height: 2em;
  width: 2em;
  margin-right: .5em;
}

#download-link:focus {
  outline: 2px solid lightgray;
}

#filter-list {
  position: absolute;
  bottom: 1em;
  width: 100%;
  left: 0;
  padding: 0;
  margin: 0;
  height: 2em;
  display: flex;
  align-items: center;
}

#filter-list li {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
  flex: 1;
}

#filter-list li button {
  height: 100%;
  border: 0;
  width: 1.7em;
  border-radius: 0 50% 50% 0;
  font-size: 130%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  background-color: white;
  cursor: pointer;
}

#filter-list li button:first-child {
  border-right: 2px solid lightgray;
  border-radius: 50% 0 0 50%;
}

li.red button {color: red;}
li.blue button {color: blue;}
li.green button {color: green;}

@media screen and (min-width: 600px) {
  #render-image canvas {
    max-width: 50%;
  }
}
</style>
