      </svg>
      Upload your picture
    </label>
    <span v-if="!!images.length && !!name">
      Picture uploaded: {{ name }}
    </span>
    <span id="upload-another" @click="resetAll" @keydown.enter="resetAll" @keydown.space="resetAll" v-if="!!images.length" tabindex="0">
      Upload another picture?
    </span>
  </div>
</template>

<script>
export default {
  name: 'upload-form',
  components: {},
  props: ['images'],
  data() {
    return {
      fileInput: null,
      name: null
    };
  },
  mounted() {
    this.fileInput = document.querySelector('#picture-input');
  },
  methods: {
    onImageInputChange(e) {
      if(!e.target.files) { return; }

      const image = e.target.files[0];
      this.name = image.name;

      createImageBitmap(image).then((bitmap) => {
        this.addImage({
          width: bitmap.width,
          height: bitmap.height,
          bitmap: bitmap,
          data: null,

          name: image.name
        });

        this.fileInput.value = null;
      });

    },
    addImage(image) {
      this.$emit('image-add', image);
    },
    resetAll(e) {
      e.preventDefault();
      this.$emit('reset-all', null);
      return false;
    }
  }
}
</script>

<style scoped>
#upload-form {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: flex .5s;
}

#upload-form.collapsed {
  flex-basis: 3em;
  flex-grow: 0;
}

#picture-input {
  width: 0;
  height: 0;
  display: block;
  overflow: hidden;
}

#picture-input + label {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  padding: 1em;
}

#picture-input + label svg {
  height: 4em;
  margin-bottom: 1em;
}

#picture-input:focus + label {
  border: 2px solid lightgray;
}

#upload-another {
  margin-top: .5em;
  cursor: pointer;
}

#upload-another:focus {
  outline: 2px solid lightgray;
}
</style>